<template>
    <div style="width: 100%;">
        <!-- 封面 start -->
        <div>
            <img class="topCover" :src="this.resData.activityIcon">
        </div>
        <!-- 封面 end -->
        <div class="container">
            <!-- 标题 -->
            <div>
                <p class="activityName">{{this.resData.activityTitle}}</p>
                <p class="activityAbout">{{this.resData.time}}·{{this.resData.activityFrom}}</p>
            </div>
            <!-- 活动时间地址 -->
            <div>
                <p class="label">时间</p>
                <p class="activityInfo">{{this.resData.activityTime}}</p>
                <p class="label">招募人数</p>
                <p class="activityInfo">{{this.resData.activityRecruitAmount}} 人</p>
                <p class="label">地址</p>
                <p class="activityInfo">{{this.resData.activityAddress}}</p>
            </div>
            <!-- 报名人数 -->
            <div style="marginBottom:20px;">
                <activityApplyFor :resData='this.resData.activityApplyForList' />
            </div>

            <!-- 相关活动 -->
            <div>
                 <P class="aboutActivityLable">活动回顾</P>
                 <activityListModule :resData='this.resData.activityList'/>
            </div>

            <!-- 正文 --> 
            <div @click="handleChangeFZ">
               <button>12px</button>
               <button>14px</button>
               <button>16px</button>
               <button>18px</button>
            </div>
            
            <div class="content normalFZ" ref="content">
                .....正文 Lorem ipsum dolor sit amet consectetur adipisicing elit.  Provident aliquid quibusdam quo itaque necessitatibus autem quis sint exercitationem dolorum debitis vero quod  eligendi, numquam, a aliquam volup
            </div>

            <!-- 相关标签 -->
            <div>
                <aboutTags :resData='this.resData.aboutTagsList'/>
            </div>

            <!-- 相关活动 -->
            <div>
                <P class="aboutActivityLable">相关活动</P>
                 <activityListModule :resData='this.resData.activityList'/>
            </div>
            
        </div>

        <!-- loading -->
        <div :class="this.isLoading?'showLoad':'hideLoad'" class="shade"  >
           <img class="loading" src="../assets/svg/Spinner-1s-200px.svg">
        </div>
    </div>
</template>
<script>
import activityListModule from "../components/activityList.vue"; //相关活动列表
import aboutTags from "../components/aboutTags.vue"; //标签
import activityApplyFor from "../components/activityApplyFor.vue"; //活动报名
export default {
  components: { activityListModule, aboutTags, activityApplyFor },
  data() {
    return {
      isLoading: false,
      resData: {}
    };
  },
  methods: {
    getData(data) {
      this.resData = data;
    },
    handleChangeFZ(e) {
        let dom  = this.$refs.content
        dom.classList.remove('smallFZ','normalFZ','largeFZ','largestFZ')
      // console.log(e)
      if (e.target.textContent == "12px") {
        // console.log(this.$refs.content);
        // this.$refs.content.className = "content smallFZ";
        alert(12)
         dom.classList.add('smallFZ')
      } else if (e.target.textContent == "14px") {
        // this.$refs.content.className = "content normalFZ";
        alert(14)
        dom.classList.add('normalFZ')
      } else if (e.target.textContent == "16px") {
        // this.$refs.content.className = "content largeFZ";
        alert(16)
        dom.classList.add('largeFZ')
      } else if (e.target.textContent == "18px") {
        // this.$refs.content.className = "content largestFZ";
        alert(18)
        dom.classList.add('largestFZ')
      }
    },
    openLoad() {
      this.isLoading = true;
    },
    closeLoad() {
      this.isLoading = false;
    }
  },
  mounted() {},
  created() {
    const resData = {
      activityId: 110,
      activityStatus: 0, // 0 活动ing 1 活动over
      activityIcon:
        "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
      activityTitle: "才艺双全邦帮赛，邦克仕 & 数字尾巴深圳沙龙",
      time: "1小时前",
      activityFrom: "来自沙龙",
      activityTime: "2016-10-23（周日）14:30 至 16:30",
      activityRecruitAmount: 30,
      activityAddress:
        "广东省深圳市南山区登良路南油第二工业区恒裕中心一楼A栋105 漫猫咖啡厅",
      activityList: [
        {
          activityStatus: true,
          activityName: "手机标配无线充电有没有必要？",
          time: "1 小时前",
          activityFrom: "深夜俱乐部",
          activityIcon:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          activityId: 110
        },
        {
          activityStatus: true,
          activityName: "手机标配无线充电有没有必要？",
          time: "1 小时前",
          activityFrom: "深夜俱乐部",
          activityIcon:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          activityId: 110
        },
        {
          activityStatus: true,
          activityName: "手机标配无线充电有没有必要？",
          time: "1 小时前",
          activityFrom: "深夜俱乐部",
          activityIcon:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          activityId: 110
        },
        {
          activityStatus: true,
          activityName: "手机标配无线充电有没有必要？",
          time: "1 小时前",
          activityFrom: "深夜俱乐部",
          activityIcon:
            "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
          activityId: 110
        }
      ],
      activityApplyForList: {
        applyUserList: [
          {
            userId: 110,
            userIcon:
              "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3744872283,1870099043&fm=27&gp=0.jpg"
          },
          {
            userId: 110,
            userIcon:
              "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3744872283,1870099043&fm=27&gp=0.jpg"
          },
          {
            userId: 110,
            userIcon:
              "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3744872283,1870099043&fm=27&gp=0.jpg"
          },
          {
            userId: 110,
            userIcon:
              "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3744872283,1870099043&fm=27&gp=0.jpg"
          }
        ],
        applyTotal: 6,
        // 0 报名 1 报名结束 2 公布名单 3活动结束
        activityStatus: 0,
        deadlineTime: "02-12",
        isApply: false
      },
      aboutTagsList: [
        {
          tagId: 110,
          tagName: "大疆"
        },
        {
          tagId: 120,
          tagName: "无人机"
        }
      ]
    };
    this.getData(resData);
  }
};
</script>
<style lang='less' scoped>
@m: 750/375;

/* 顶部封面 */

.topCover {
  display: block;
  width: 375px * @m;
  height: 285px * @m;
  margin-bottom: 30px * @m;
}
// 容器
.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px solid transparent;
  font-size: 14px * @m;
  margin-top: 30px * @m;
  background: #ffffff;
  height: auto;
}
/* 活动相关信息 */

.activityName {
  font-size: 28px * @m;
  margin-bottom: 10px * @m;
  padding: 0 5px * @m;
}

.activityAbout {
  font-size: 12px * @m;
  color: #c6c6c6;
  margin-bottom: 30px * @m;
  padding: 0 5px * @m;
}

.activityInfo {
  margin-bottom: 20px * @m;
  font-size: 16px * @m;
}

.label {
  font-size: 16px * @m;
  color: #b9b9b9;
  margin-bottom: 10px * @m;
}

/* 报名人数 */

.activityUser {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-bottom: 10px * @m;
}
.activityUser ul {
  display: flex;
  flex-wrap: nowrap;
}

.activityUser li {
  margin-right: 6px * @m;
}

.activityUser li img {
  display: block;
  width: 36px * @m;
  height: 36px * @m;
  border-radius: 100%;
}

.activityUser .btn {
  background-image: linear-gradient(-180deg, #9adaff 0%, #61b3ff 100%);
  box-shadow: 0 0 30px * @m 0 rgba(97, 179, 255, 0.3);
  border-radius: 100px * @m;
  color: #fff;
  width: 100px * @m;
  height: 40px * @m;
  line-height: 40px * @m;
  text-align: center;
}

.activityStatus {
  font-size: 12px * @m;
  color: #c6c6c6;
}
// 正文
.container .content {
  margin-bottom: 30px * @m;
  height: auto;
}
.container .content.smallFZ {
  font-size: 12px * @m;
}
.container .content.normalFZ {
  font-size: 14px * @m;
}
.container .content.largeFZ {
  font-size: 16px * @m;
}
.container .content.largestFZ {
  font-size: 18px * @m;
}

button {
  font-size: 20px * @m;
}

// 相关活动
.aboutActivityLable {
  font-size: 14px * @m;
  width: 100%;
  height: 20px * @m;
  margin-bottom: 20px * @m;
}

// loading层
.hideLoad {
  display: none !important;
}
.showLoad {
  display: block !important;
}

.shade {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #fff;
  z-index: 99;
}
.loading {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>




