<template>
   <div>
        <div class="activityUser">
            <ul>
                <li v-for="(item,index) in resData.applyUserList" :key="index" @click="personalInformation(item.userId)">
                    <img :src="item.userIcon">
                </li>
                <li v-if="this.resData.applyTotal>5">
                    <div class="manyMore">
                        +{{moreApply}}
                    </div>
                </li>
            </ul>
            <div v-if="resData.activityStatus==0 && resData.isApply==false " class="btn" @click='this.handleApply'>我要报名</div>
            <div v-else-if="resData.activityStatus==0 || resData.activityStatus==1  && resData.isApply" class="disableBtn" >已报名</div>
            <div v-else-if="resData.activityStatus==1 && resData.isApply==false" class="disableBtn" >报名结束</div>
            <div v-else-if="resData.activityStatus==2" class="btn" @click="this.handleViewList">查看名单</div>
            <div v-else-if="resData.activityStatus==3" class="disableBtn" >活动结束</div>
        </div>
        <p v-if="resData.activityStatus==0 ||resData.activityStatus==2 " class="activityStatus">报名截止日期: {{resData.deadlineTime}}</p>
        <p v-else-if="resData.activityStatus==1 || resData.activityStatus==3 " class="activityStatus">报名已截止 , 共有: {{resData.applyTotal}} 人报名</p>
    </div>
</template>
<script>
export default {
    props: ["resData"],
  components: {},
  data() {
    return {
    //   resData: {
    //     applyUserList: [
    //       {
    //         userId: 110,
    //         userIcon:
    //           "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3744872283,1870099043&fm=27&gp=0.jpg"
    //       },
    //       {
    //         userId: 110,
    //         userIcon:
    //           "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3744872283,1870099043&fm=27&gp=0.jpg"
    //       },
    //       {
    //         userId: 110,
    //         userIcon:
    //           "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3744872283,1870099043&fm=27&gp=0.jpg"
    //       },
    //       {
    //         userId: 110,
    //         userIcon:
    //           "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3744872283,1870099043&fm=27&gp=0.jpg"
    //       }
    //     ],
    //     applyTotal: 6,
    //     // 0 报名 1 报名结束 2 公布名单 3活动结束
    //     activityStatus: 0,
    //     deadlineTime: "02-12",
    //     isApply: false
    //   }
    };
  },
  computed: {
    moreApply() {
      if (this.resData.applyTotal > 5 && this.resData.applyTotal < 1000) {
        return this.resData.applyTotal - 4;
      } else {
        return 999;
      }
    }
  },
  methods: {
    handleApply() {
      alert("我要报名");
    },
    handleViewList() {
      alert("查看名单");
    },
    personalInformation(id){
        alert('user ID '+ id)
    }
  },
  created() {},
  mounted() {}
};
</script>
<style lang='less' scoped>
@m: 750/375;
/* 报名人数 */

.activityUser {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin-bottom: 10px * @m;
}
.activityUser ul {
  display: flex;
  flex-wrap: nowrap;
}

.activityUser li {
  margin-right: 6px * @m;
}

.activityUser li img {
  display: block;
  width: 36px * @m;
  height: 36px * @m;
  border-radius: 100%;
}
.activityUser .manyMore {
  font-size: 14px * @m;
  width: 36px * @m;
  height: 36px * @m;
  border-radius: 100%;
  background-image: linear-gradient(45deg, #b5c1d2 0%, #e7effb 100%);
  color: #ffffff;
  letter-spacing: 0;
  text-align: center;
  line-height: 36px * @m;
}

.activityUser .btn {
  background-image: linear-gradient(-180deg, #9adaff 0%, #61b3ff 100%);
  box-shadow: 0 0 30px * @m 0 rgba(97, 179, 255, 0.3);
  border-radius: 100px * @m;
  color: #fff;
  width: 100px * @m;
  height: 40px * @m;
  line-height: 40px * @m;
  text-align: center;
}

.activityUser .disableBtn {
  background: #f5f7f9;
  border-radius: 100px * @m;
  color: #c6c6c6;
  width: 100px* @m;
  height: 40px* @m;
  line-height: 40px * @m;
  text-align: center;
}

.activityStatus {
  font-size: 12px * @m;
  color: #c6c6c6;
}
</style>




